import React from 'react';
import s from './Nav.module.scss';
import {Link} from "react-router-dom";

const Nav = () => {
    return (
        <div className={s.headersLinks}>
            <Link  to={'/headphones'} ></Link>
            <Link  to={'/antennas'}></Link>
            <Link  to={'/batteries'} ></Link>
            <Link  to='aboutUs' > </Link>

        </div>
    );
};

export default Nav;